import request from '@/utils/request'

/**
 * 获取聊天会话列表
 */
export function getChatSessions() {
  return request({
    url: '/api/chat/sessions',
    method: 'get'
  })
}

/**
 * 创建新的聊天会话
 * @param {String} modelType 模型类型
 */
export function createChatSession(modelType) {
  return request({
    url: '/api/chat/session',
    method: 'post',
    params: { modelType }
  })
}

/**
 * 获取会话的消息历史
 * @param {Number} sessionId 会话ID
 */
export function getChatMessages(sessionId) {
  return request({
    url: `/api/chat/messages/${sessionId}`,
    method: 'get'
  })
}

/**
 * 删除聊天会话
 * @param {Number} sessionId 会话ID
 */
export function deleteChatSession(sessionId) {
  return request({
    url: `/api/chat/session/${sessionId}`,
    method: 'delete'
  })
}

/**
 * 重命名聊天会话
 * @param {Number} sessionId 会话ID
 * @param {String} title 新标题
 */
export function renameChatSession(sessionId, title) {
  return request({
    url: `/api/chat/session/${sessionId}/title`,
    method: 'put',
    data: { title }
  })
}

/**
 * 发送聊天消息(非流式)
 * @param {Object} data 包含sessionId和message的对象
 */
export function sendChatMessage(data) {
  return request({
    url: '/api/chat/send',
    method: 'post',
    data
  })
}

/**
 * 发送消息到后端(直接方法)
 * @param {String} prompt 要发送的提示文字
 * @returns {Promise<String>} 返回后端响应文本
 */
export async function sendMessageToBackend(prompt) {
  const baseUrl = process.env.VUE_APP_BASE_API || 'http://localhost:5000';
  const response = await fetch(`${baseUrl}/chat?prompt=${encodeURIComponent(prompt)}`, {
    method: 'GET',
    headers: {
      'Accept': '*/*',
    },
    mode: 'cors',
    credentials: 'omit',
  });

  if (!response.ok) {
    throw new Error(`HTTP error! status: ${response.status}`);
  }

  return await response.text();
}

/**
 * 初始化流式响应
 * @param {Object} data 包含sessionId、message和modelType的对象
 */
export function initStreamResponse(data) {
  return request({
    url: '/api/chat/stream',
    method: 'post',
    data
  })
}

/**
 * 获取支持的模型列表
 */
export function getModelTypes() {
  return request({
    url: '/api/chat/models',
    method: 'get'
  })
}

/**
 * 创建EventSource用于流式接收消息
 * @param {Number} sessionId 会话ID
 * @returns {EventSource} 返回EventSource实例
 */
export function createStreamConnection(sessionId) {
  const baseUrl = process.env.VUE_APP_BASE_API || 'http://localhost:5000'
  return new EventSource(`${baseUrl}/api/chat/stream/${sessionId}`)
} 